import React, { useState, useEffect } from "react";
import { TRANSACTION_COUNT } from "core/constants";
import {
  deleteHashAndCategoryArray,
  fetchHashAndCategoryArray,
  saveHashAndCategoryArray,
} from "services/hashAndCategoryArray";
import { fetchRules } from "services/rules";
import { fetchTransactions } from "services/transactions";
import { calculateHashAndCategoryArray } from "process/categories";
import cn from "classnames";
import s from "./CategoriesPage.module.css";

// ----------
// COMPONENT:
const CategoriesPage = () => {
  // STATE:
  const [
    hashAndCategoryArrayFetched,
    setHashAndCategoryArrayFetched,
  ] = useState({});
  const [
    hashAndCategoryArrayCalculated,
    setHashAndCategoryArrayCalculated,
  ] = useState({});

  // EFFECTS - GET DATA:
  useEffect(() => {
    const fetchData = async () => {
      const hca = await fetchHashAndCategoryArray();
      console.log("*** fetched:", hca[0].array);
      setHashAndCategoryArrayFetched(hca);
    };
    fetchData();
    return () => {}; // on unmount
  }, []);

  // FUNCTIONS:
  const calculateCategories = async () => {
    const r = await fetchRules();
    console.log("*** rules:", r.length);
    const t = await fetchTransactions(0, TRANSACTION_COUNT);
    console.log("*** transactions:", t.length);
    const hca = await calculateHashAndCategoryArray(t, r);
    console.log("*** hca length:", Object.keys(hca).length);
    setHashAndCategoryArrayCalculated(hca);
  };
  const saveCategories = async () => {
    const obj = { id: 1, array: hashAndCategoryArrayCalculated };
    const result = saveHashAndCategoryArray(obj);
    console.log("*** save result:", result);
  };
  const deleteCategories = async () => {
    const result = deleteHashAndCategoryArray();
    console.log("*** delete result:", result);
  };

  console.log(
    "*** hashAndCategoryArrayFetched length:",
    Object.keys(hashAndCategoryArrayFetched).length
  );
  console.log(
    "*** hashAndCategoryArrayCalculated length:",
    Object.keys(hashAndCategoryArrayCalculated).length
  );

  // RENDER:
  return (
    <div className={cn("page", s.root)}>
      <h3>Categories</h3>
      <div>
        Category for all transactions:&nbsp;&nbsp;&nbsp;
        <button onClick={calculateCategories}>Calculate</button>
        &nbsp;&nbsp;&nbsp;
        <button onClick={deleteCategories}>Delete</button>
        &nbsp;&nbsp;&nbsp;
        <button onClick={saveCategories}>Save</button>
      </div>
    </div>
  );
};

export default CategoriesPage;
