import React from "react";
import { Icon } from "components";
import s from "./PageHeaderButtons.module.css";

type Props = {
  network: boolean;
  onNetworkClick: () => void;
  mask: boolean;
  onMaskClick: () => void;
};

// ----------
// COMPONENT:
const PageHeaderButtons = ({
  network,
  onNetworkClick,
  mask,
  onMaskClick,
}: Props) => {
  return (
    <div className={s.root + " flexRowLeft"}>
      <button onClick={onNetworkClick}>
        <Icon name={network ? "networkOn" : "networkOff"} />
      </button>
      <button onClick={onMaskClick}>
        <Icon name={mask ? "maskOn" : "maskOff"} />
      </button>
    </div>
  );
};

export default PageHeaderButtons;
