import React from "react";
import { useHistory } from "react-router-dom";
import { PercentageChartPage, TrendChartPage } from "pages";
import cn from "classnames";
import s from "./HomePage.module.css";

const HomePage = () => {
  const history = useHistory();

  const onBoxClick = (target: string) => {
    history.replace(target);
  };

  return (
    <div className={cn("page", s.root)}>
      <h3>Home page</h3>

      {/* <div className={cn("flexRow", "pageBoxes")}>
        <PercentageChartPage
          isBoxed={true}
          onBoxClick={onBoxClick}
          network={false}
        />

        <TrendChartPage
          isBoxed={true}
          onBoxClick={onBoxClick}
          network={false}
        />
      </div> */}
    </div>
  );
};

export default HomePage;
