import { Json } from "core/types";
import { URL } from "./constants";
import { fetchUrl, postJson, deleteUrl } from "./utils";

export const fetchHashAndCategoryArray = async () => {
  return await fetchUrl(URL.HASHANDCATEGORYARRAY);
};

export const saveHashAndCategoryArray = (dataJson: Json) => {
  return postJson(dataJson, URL.HASHANDCATEGORYARRAY);
};

export const deleteHashAndCategoryArray = () => {
  return deleteUrl(URL.HASHANDCATEGORYARRAY + "/1");
};
