import React, { useState } from "react";
import { Route, Switch } from "react-router-dom";
import { AppContext, DefaultContext } from "appContext.ts";
import {
  CategoriesPage,
  HomePage,
  PercentageChartPage,
  RulesPage,
  TransactionsPage,
  TrendChartPage,
} from "./pages";
import { PageHeader, PageFooter } from "components";

const route = (path, page) => <Route path={path}>{page}</Route>;

function App() {
  // STATE:
  const [isNetwork, setIsNetwork] = useState(true);
  const [isMasked, setIsMasked] = useState(true);

  // CLICK:
  const onNetworkClick = () => setIsNetwork(true);
  const onMaskClick = () => setIsMasked(true);
  // const onNetworkClick = () => setIsNetwork(!isNetwork);
  // const onMaskClick = () => setIsMasked(!isMasked);

  return (
    <AppContext.Provider value={DefaultContext}>
      <PageHeader
        network={isNetwork}
        onNetworkClick={onNetworkClick}
        mask={isMasked}
        onMaskClick={onMaskClick}
      />

      <Switch>
        <Route
          path="/transactions/transaction=:id"
          render={({ match, isMasked }) => (
            <TransactionsPage match={match} isMasked={isMasked} />
          )}
        />

        {route(
          "/transactions",
          <TransactionsPage network={isNetwork} isMasked={isMasked} />
        )}

        {route("/categories", <CategoriesPage />)}

        <Route
          path="/rules/transaction=:id"
          render={({ match }) => <RulesPage match={match} mask={isMasked} />}
        />

        {route("/rules", <RulesPage mask={isMasked} />)}
        {route("/percentage", <PercentageChartPage network={isNetwork} />)}
        {route("/trend", <TrendChartPage network={isNetwork} />)}
        {route("/", <HomePage />)}
      </Switch>

      <PageFooter />
    </AppContext.Provider>
  );
}

export default App;
